import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog';
import { 
  Users, 
  CreditCard, 
  TrendingUp, 
  DollarSign, 
  Plus, 
  Minus,
  Search,
  Filter,
  Download,
  Eye,
  Edit,
  AlertTriangle,
  CheckCircle,
  Clock,
  Infinity
} from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';

const CreditManagement = () => {
  const [users, setUsers] = useState([]);
  const [analytics, setAnalytics] = useState(null);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedUser, setSelectedUser] = useState(null);
  const [adjustmentModal, setAdjustmentModal] = useState(false);
  const [adjustmentAmount, setAdjustmentAmount] = useState('');
  const [adjustmentReason, setAdjustmentReason] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);

  useEffect(() => {
    fetchData();
  }, [currentPage]);

  const fetchData = async () => {
    try {
      setLoading(true);
      const [usersResponse, analyticsResponse] = await Promise.all([
        axiosInstance.get(`/api/credits/admin/users?page=${currentPage}&limit=20`),
        axiosInstance.get('/api/credits/admin/analytics?days=30')
      ]);
      
      setUsers(usersResponse.data.data.users);
      setTotalPages(usersResponse.data.data.pagination.totalPages);
      setAnalytics(analyticsResponse.data.data);
    } catch (error) {
      console.error('Error fetching credit data:', error);
      toast.error('Failed to load credit data');
    } finally {
      setLoading(false);
    }
  };

  const handleAdjustCredits = async () => {
    if (!selectedUser || !adjustmentAmount) {
      toast.error('Please enter an adjustment amount');
      return;
    }

    try {
      await axiosInstance.post('/api/credits/admin/adjust', {
        userId: selectedUser.user_id,
        amount: parseFloat(adjustmentAmount),
        reason: adjustmentReason || 'Admin adjustment'
      });

      toast.success(`Credits ${adjustmentAmount > 0 ? 'added to' : 'deducted from'} ${selectedUser.email}`);
      setAdjustmentModal(false);
      setAdjustmentAmount('');
      setAdjustmentReason('');
      setSelectedUser(null);
      fetchData();
    } catch (error) {
      console.error('Error adjusting credits:', error);
      toast.error('Failed to adjust credits');
    }
  };

  const filteredUsers = users.filter(user =>
    user.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.first_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.last_name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.username?.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const formatCredits = (credits) => {
    if (credits === null || credits === undefined) return '0';
    return parseFloat(credits).toLocaleString('en-US', { 
      minimumFractionDigits: 0, 
      maximumFractionDigits: 2 
    });
  };

  const getCreditStatusColor = (availableCredits) => {
    if (availableCredits <= 0) return 'text-red-600';
    if (availableCredits <= 10) return 'text-yellow-600';
    return 'text-green-600';
  };

  const getCreditStatusBadge = (availableCredits) => {
    if (availableCredits <= 0) return { variant: 'destructive', text: 'No Credits' };
    if (availableCredits <= 10) return { variant: 'secondary', text: 'Low Credits' };
    if (availableCredits <= 50) return { variant: 'outline', text: 'Medium' };
    return { variant: 'default', text: 'Good' };
  };

  if (loading) {
    return (
      <div className="p-6 space-y-6">
        <div className="flex justify-between items-center">
          <h1 className="text-3xl font-bold">Credit Management</h1>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          {[...Array(4)].map((_, i) => (
            <Card key={i}>
              <CardContent className="p-6">
                <div className="animate-pulse">
                  <div className="h-4 bg-gray-200 rounded w-3/4 mb-2"></div>
                  <div className="h-8 bg-gray-200 rounded w-1/2"></div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    );
  }

  return (
    <div className="p-6 space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Credit Management</h1>
          <p className="text-gray-600 mt-1">Monitor and manage user credits across the platform</p>
        </div>
        <div className="flex space-x-2">
          <Button variant="outline">
            <Download className="h-4 w-4 mr-2" />
            Export Data
          </Button>
        </div>
      </div>

      {/* Analytics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Users</p>
                <p className="text-2xl font-bold text-blue-600">
                  {users.length}
                </p>
              </div>
              <Users className="h-8 w-8 text-blue-600" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Total Revenue</p>
                <p className="text-2xl font-bold text-green-600">
                  ${analytics?.payments?.total_revenue ? (analytics.payments.total_revenue / 100).toFixed(2) : '0.00'}
                </p>
              </div>
              <DollarSign className="h-8 w-8 text-green-600" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Credits Sold</p>
                <p className="text-2xl font-bold text-purple-600">
                  {analytics?.payments?.total_credits_sold ? formatCredits(analytics.payments.total_credits_sold) : '0'}
                </p>
              </div>
              <CreditCard className="h-8 w-8 text-purple-600" />
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">Credits Used</p>
                <p className="text-2xl font-bold text-orange-600">
                  {analytics?.usage?.total_credits_consumed ? formatCredits(analytics.usage.total_credits_consumed) : '0'}
                </p>
              </div>
              <TrendingUp className="h-8 w-8 text-orange-600" />
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Main Content */}
      <Tabs defaultValue="users" className="w-full">
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="users">User Credits</TabsTrigger>
          <TabsTrigger value="transactions">Recent Transactions</TabsTrigger>
          <TabsTrigger value="analytics">Analytics</TabsTrigger>
        </TabsList>

        <TabsContent value="users" className="space-y-4">
          {/* Search and Filters */}
          <div className="flex justify-between items-center">
            <div className="flex items-center space-x-2">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
                <Input
                  placeholder="Search users..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="pl-10 w-64"
                />
              </div>
              <Button variant="outline">
                <Filter className="h-4 w-4 mr-2" />
                Filter
              </Button>
            </div>
          </div>

          {/* Users Table */}
          <Card>
            <CardContent className="p-0">
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>User</TableHead>
                    <TableHead>Total Credits</TableHead>
                    <TableHead>Used Credits</TableHead>
                    <TableHead>Available Credits</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Last Purchase</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredUsers.map((user) => {
                    const statusBadge = getCreditStatusBadge(parseFloat(user.available_credits || 0));
                    return (
                      <TableRow key={user.user_id}>
                        <TableCell>
                          <div>
                            <div className="font-medium">
                              {user.first_name} {user.last_name}
                            </div>
                            <div className="text-sm text-gray-500">{user.email}</div>
                          </div>
                        </TableCell>
                        <TableCell className="font-medium">
                          {formatCredits(user.total_credits)}
                        </TableCell>
                        <TableCell className="text-red-600">
                          {formatCredits(user.used_credits)}
                        </TableCell>
                        <TableCell className={getCreditStatusColor(parseFloat(user.available_credits || 0))}>
                          <div className="font-medium">
                            {formatCredits(user.available_credits)}
                          </div>
                        </TableCell>
                        <TableCell>
                          <Badge variant={statusBadge.variant}>
                            {statusBadge.text}
                          </Badge>
                        </TableCell>
                        <TableCell>
                          {user.last_purchase_at ? 
                            new Date(user.last_purchase_at).toLocaleDateString() : 
                            'Never'
                          }
                        </TableCell>
                        <TableCell>
                          <div className="flex space-x-2">
                            <Button
                              size="sm"
                              variant="outline"
                              onClick={() => {
                                setSelectedUser(user);
                                setAdjustmentModal(true);
                              }}
                            >
                              <Edit className="h-4 w-4" />
                            </Button>
                            <Button size="sm" variant="outline">
                              <Eye className="h-4 w-4" />
                            </Button>
                          </div>
                        </TableCell>
                      </TableRow>
                    );
                  })}
                </TableBody>
              </Table>
            </CardContent>
          </Card>

          {/* Pagination */}
          <div className="flex justify-between items-center">
            <p className="text-sm text-gray-600">
              Showing {filteredUsers.length} of {users.length} users
            </p>
            <div className="flex space-x-2">
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
                disabled={currentPage === 1}
              >
                Previous
              </Button>
              <Button
                variant="outline"
                size="sm"
                onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
                disabled={currentPage === totalPages}
              >
                Next
              </Button>
            </div>
          </div>
        </TabsContent>

        <TabsContent value="transactions">
          <TransactionHistory />
        </TabsContent>

        <TabsContent value="analytics">
          <CreditAnalytics analytics={analytics} />
        </TabsContent>
      </Tabs>

      {/* Credit Adjustment Modal */}
      <Dialog open={adjustmentModal} onOpenChange={setAdjustmentModal}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Adjust Credits</DialogTitle>
            <DialogDescription>
              Adjust credits for {selectedUser?.email}
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4">
            <div>
              <label className="text-sm font-medium">Amount</label>
              <Input
                type="number"
                placeholder="Enter amount (positive to add, negative to deduct)"
                value={adjustmentAmount}
                onChange={(e) => setAdjustmentAmount(e.target.value)}
              />
            </div>
            <div>
              <label className="text-sm font-medium">Reason</label>
              <Input
                placeholder="Reason for adjustment"
                value={adjustmentReason}
                onChange={(e) => setAdjustmentReason(e.target.value)}
              />
            </div>
            <div className="flex justify-end space-x-2">
              <Button variant="outline" onClick={() => setAdjustmentModal(false)}>
                Cancel
              </Button>
              <Button onClick={handleAdjustCredits}>
                Adjust Credits
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </div>
  );
};

// Transaction History Component
const TransactionHistory = () => {
  const [transactions, setTransactions] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchTransactions();
  }, []);

  const fetchTransactions = async () => {
    try {
      // This would be a new endpoint for admin transaction history
      const response = await axiosInstance.get('/api/credits/admin/transactions?limit=50');
      setTransactions(response.data.data || []);
    } catch (error) {
      console.error('Error fetching transactions:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return <div>Loading transactions...</div>;
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle>Recent Credit Transactions</CardTitle>
      </CardHeader>
      <CardContent>
        <p className="text-gray-500">Transaction history will be displayed here</p>
      </CardContent>
    </Card>
  );
};

// Credit Analytics Component
const CreditAnalytics = ({ analytics }) => {
  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <CardTitle>Credit System Analytics</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <h3 className="text-lg font-semibold mb-4">Usage Statistics</h3>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span>Total Operations:</span>
                  <span className="font-medium">{analytics?.usage?.total_operations || 0}</span>
                </div>
                <div className="flex justify-between">
                  <span>Credits Consumed:</span>
                  <span className="font-medium">{analytics?.usage?.total_credits_consumed || 0}</span>
                </div>
                <div className="flex justify-between">
                  <span>Average per Operation:</span>
                  <span className="font-medium">{analytics?.usage?.avg_credits_per_operation || 0}</span>
                </div>
              </div>
            </div>
            <div>
              <h3 className="text-lg font-semibold mb-4">Payment Statistics</h3>
              <div className="space-y-2">
                <div className="flex justify-between">
                  <span>Total Payments:</span>
                  <span className="font-medium">{analytics?.payments?.total_payments || 0}</span>
                </div>
                <div className="flex justify-between">
                  <span>Successful Payments:</span>
                  <span className="font-medium">{analytics?.payments?.successful_payments || 0}</span>
                </div>
                <div className="flex justify-between">
                  <span>Success Rate:</span>
                  <span className="font-medium">
                    {analytics?.payments?.total_payments > 0 
                      ? ((analytics.payments.successful_payments / analytics.payments.total_payments) * 100).toFixed(1)
                      : 0}%
                  </span>
                </div>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  );
};

export default CreditManagement;
